html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Slab", sans-serif;
  font-size: 1.6rem;
}
body.body-scroll {
  padding-top: 8.1rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #e8eaea;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  margin-top: 3rem;
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
}

.contenedor-oscuro {
  padding: 5rem 0;
  background-color: rgba(0, 0, 0, 0.4);
  margin: 0 auto;
}

.titulo-secundario {
  text-align: center;
  color: #005353;
}
@media (min-width: 780px) {
  .titulo-secundario {
    font-size: 4rem;
    line-height: 4rem;
    margin: 2rem;
  }
}
@media (min-width: 992px) {
  .titulo-secundario {
    font-size: 6rem;
    line-height: 6rem;
    margin: 5rem;
  }
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.margin {
  margin: 1rem;
}

.boton-primario {
  background-color: #FA4C5A;
  padding: 2.5rem;
  color: #fff;
  margin: 0 auto;
  font-size: larger;
}
@media (min-width: 780px) {
  .boton-primario {
    width: 30%;
  }
}
.boton-primario:hover {
  background-color: #FDBD01;
}

.boton-secundario {
  background-color: #005353;
  padding: 1.5rem;
  color: #fff;
  margin: 0 auto;
  font-size: larger;
}
@media (min-width: 780px) {
  .boton-secundario {
    width: 30%;
  }
}
.boton-secundario:hover {
  background-color: #FDBD01;
}

/**     UI     **/
.footer {
  background-color: #005353;
  text-align: center;
}
.footer .redes-footer {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
}

.footer-sec {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
}

.footer-nav {
  margin: 1rem;
  justify-content: center;
}

.copyright {
  position: relative;
  font-size: 1.8rem;
  color: #c2bdbd;
  margin: 0.5rem 0 0 0;
}

@media (min-width: 780px) {
  .header.fijada {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
    background-color: #005353;
  }
}

.header-container {
  background-color: #005353;
  transition: 200ms background-color, color ease-in;
  box-shadow: 0.3rem 0.3rem 0.3rem #333;
  z-index: 100;
}
.header-container:hover .navegacion-principal a {
  color: #fff;
}
.header-container:hover .navegacion-principal a:hover {
  color: #FDBD01;
}
@media (min-width: 780px) {
  .header-container.fijada {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
    background-color: #005353;
  }
  .header-container.fijada a {
    font-size: 1.8rem;
  }
  .header-container.fijada .site-header {
    padding: 0;
  }
}

.site-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 780px) {
  .site-header {
    display: flex;
    flex-direction: row;
    gap: 5rem;
    justify-content: space-between;
    align-items: center;
  }
}

.site-header {
  position: relative;
  width: 80%;
  padding: 0 0 1rem 0;
  margin: 0 auto;
}
@media (min-width: 780px) {
  .site-header {
    padding: 2.3rem;
  }
}

.logo {
  width: 15rem;
  height: 15rem;
}
@media (min-width: 780px) {
  .logo {
    position: absolute;
    top: 0;
    left: 0;
    width: 20rem;
    height: 20rem;
    margin-top: -1.27rem;
    z-index: 1;
    transition: 200ms right transform ease-out;
  }
  .logo-scale {
    transform: scale(0.5);
    top: -60px;
    margin: 0;
    left: -100px;
    transition: 200ms left ease-out;
  }
}

.navegacion-principal {
  text-align: center;
  visibility: hidden;
  opacity: 0;
}
@media (min-width: 780px) {
  .navegacion-principal {
    display: flex;
    flex-direction: row;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}

.mostrar {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
  transition: visibility 1s linear, opacity 1s linear;
}
@media (min-width: 780px) {
  .mostrar {
    display: block;
  }
}

.navegacion-principal a {
  color: #c2bdbd;
  font-size: 2rem;
  font-weight: bold;
  margin: 1rem 0;
}
@media (min-width: 780px) {
  .navegacion-principal a {
    margin: 0 3rem 0 0;
  }
  .navegacion-principal a:last-of-type {
    margin-right: 0;
  }
}

.mobile-menu {
  width: 5rem;
  height: auto;
}
@media (min-width: 780px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu:hover {
  filter: invert(20%);
}

.carrito {
  width: 5rem;
  height: auto;
}
.carrito:hover {
  filter: invert(20%);
}

.cart-mobile {
  display: block;
}
@media (min-width: 780px) {
  .cart-mobile {
    display: none;
  }
}

.cart-desktop {
  width: 3.5rem;
  height: auto;
  display: none;
}
@media (min-width: 780px) {
  .cart-desktop {
    display: block;
  }
}

h4 {
  color: blanco;
}

.quines-somos h2 {
  color: #fff;
}

.productos-main {
  margin: 5rem auto;
  display: flex;
  flex-direction: column-reverse;
  gap: 2rem;
}
.productos-main h3 {
  font-size: 3rem;
  color: #5DAE70;
}
@media (min-width: 780px) {
  .productos-main {
    align-items: center;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
  .productos-main h3 {
    font-size: 5rem;
  }
}

@media (min-width: 780px) {
  .org h3, .org p {
    text-align: start;
  }
  .org2 {
    grid-auto-flow: column;
  }
  .org2 .huerta-productos {
    grid-column: 2;
  }
  .org2 .huerta-productos h3, .org2 .huerta-productos p {
    text-align: start;
  }
}
.nosotros-container {
  height: auto;
  width: 100%;
}

.nosotros-contenedor {
  margin: 10rem auto;
  text-align: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 5rem;
}

.nosotros-contenido {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  color: #fff;
  width: 70%;
}
.nosotros-contenido h2 {
  text-align: center;
}
.nosotros-contenido p {
  color: #fff;
}

.titulo-nosotros {
  height: 50rem !important;
  margin-bottom: 10rem !important;
}
.titulo-nosotros p {
  font-size: 2.7rem;
  color: #FDBD01;
}

.trayectoria {
  background-color: #e8eaea;
  padding: 5rem;
  margin: 0 auto;
}

.trayectoria-contenido p {
  color: #005353;
}
.trayectoria-contenido .img-tr {
  width: fit-content;
}
@media (min-width: 780px) {
  .trayectoria-contenido .trayectoria-flex {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 3rem;
  }
}

.no-webp {
  background-image: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/build/img/portada-imagen.jpg);
}

.webp {
  background-image: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/build/img/portada-imagen.webp);
}

.no-webp, .webp {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.portada-container {
  width: 100%;
  height: 60rem;
  display: flex;
  flex-direction: column;
  place-content: center;
  text-align: center;
  margin-bottom: 15rem;
}

h1 {
  font-family: "Roboto Slab", sans-serif;
  font-weight: 700;
  color: #fff;
}
@media (min-width: 780px) {
  h1 {
    font-size: 6rem;
    line-height: 7rem;
  }
}
@media (min-width: 992px) {
  h1 {
    font-size: 8rem;
    line-height: 8rem;
  }
}

.portada-container h1 span {
  color: #FDBD01;
}

.main-slider {
  background-color: #e8eaea;
}

.sec-productos {
  text-align: center;
  color: #005353;
  padding: 2rem 0 15rem 0;
}
@media (min-width: 780px) {
  .sec-productos h2 {
    font-size: 4rem;
    line-height: 4rem;
    margin: 2rem;
  }
}
@media (min-width: 992px) {
  .sec-productos h2 {
    font-size: 6rem;
    line-height: 6rem;
    margin: 5rem;
  }
}

.slider__container {
  display: grid;
  grid-template-columns: 50px 1fr 50px;
  align-items: center;
  gap: 2rem;
  margin-bottom: 0rem;
  position: relative;
}

.slider__body {
  grid-column: 2/3;
  grid-row: 1/2;
  opacity: 0;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  transition: opacity 800ms ease-out;
}

.slider__body--show {
  opacity: 1;
  pointer-events: unset;
}

.slider__picture {
  display: block;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}

#slider__img {
  object-fit: cover;
  max-width: 100%;
  max-height: 500px;
  position: relative;
  z-index: 0;
}

.slider__arrow {
  cursor: pointer;
  border-radius: 50%;
}
.slider__arrow:hover {
  background-color: rgba(194, 189, 189, 0.2274509804);
}

.slider__text {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: space-around;
  width: 70%;
  margin: 0 auto;
  position: absolute;
  z-index: 2;
  bottom: 0px;
  left: 15rem;
  color: #e8eaea;
}
.slider__text h3 {
  font-size: 4rem;
}
.slider__text p {
  margin-bottom: 5rem;
  font-size: 2rem;
  left: 10rem;
  color: #e8eaea;
}

.prueba1 {
  margin: 2.5rem;
}

.galeria-bg {
  background-color: #e8eaea;
  padding: 2rem 2rem 15rem 2rem;
}

.galeria {
  margin: 2rem 5rem;
}
.galeria .galeria-titulo {
  font-size: 6rem;
  text-align: center;
  margin: 5rem 0;
  color: #005353;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 780px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria-imagenes:hover {
  cursor: pointer;
}

.imagen-galeria {
  position: inherit;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  align-content: center;
}
.overlay picture {
  display: block;
  max-width: 100rem;
  box-shadow: 0px 0px 33px 9px rgba(0, 0, 0, 0.4);
}

.boton-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
  color: #000;
  font-weight: 700;
  display: grid;
  place-items: center;
  grid-row: 1/2;
  font-family: Arial, Helvetica, sans-serif;
  opacity: 0.7;
}
.boton-cerrar:hover {
  cursor: pointer;
}

.contacto_redes-contenedor {
  background-color: #e8eaea;
  padding: 5rem 0 15rem;
}

