}

.sidebar {
  padding: 2rem;
  color: #fff;
}
.sidebar .indice {
  list-style: none;
  margin: 0 0 0 1rem;
  padding: 0;
  line-height: 3rem;
}
.sidebar .indice li:hover {
  cursor: pointer;
  text-decoration: underline;
}

.filtro {
  display: flex;
  justify-content: flex-end;
  margin-right: 4rem;
}
.filtro .lista {
  background-color: #fff;
  color: #000;
  padding: 1rem 2rem;
  border-radius: 10%;
  border: 3px solid #666363;
}
.filtro .lista:hover {
  border: 3px solid #5DAE70;
  transition: 200ms border ease-in;
}

.productos-tienda {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 2rem;
}

.producto {
  display: flex;
  position: relative;
  flex-direction: column;
}
.producto .boton-primario {
  width: 100%;
}
.producto h3 {
  position: absolute;
  top: 15rem;
  right: 8rem;
  z-index: 1;
}

.producto_img {
  position: relative;
}
.producto_img p {
  position: absolute;
  left: 2rem;
  bottom: 0;
  color: #fff;
  z-index: 2;
}

.contacto-formulario {
  padding: 10rem 0;
  text-align: center;
}
.contacto-formulario h3 {
  color: #e8eaea;
  margin-bottom: 4rem;
}

.contacto-contenedor {
  align-items: center;
}
.contacto-contenedor p {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2rem;
}

.contacto-formulario p {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2rem;
  padding-top: 1rem;
}

.formulario {
  margin-top: 3rem;
  height: 40rem;
  background-color: rgba(194, 189, 189, 0.2274509804);
  border-radius: 5px;
  margin: 2rem auto 0 auto;
  width: 75%;
  padding: 0rem 2rem 3rem 1rem;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 0px 0px 28px 11px rgba(0, 0, 0, 0.55);
}

.campo {
  display: flex;
  margin-bottom: 1rem;
  gap: 2rem;
}

.campo__label {
  flex: 0 0 9rem;
  text-align: right;
  color: #e8eaea;
  font-weight: bold;
}

.campo__field {
  flex: 1;
  border: 1px solid var(--gris);
}

.campo__field--textarea {
  height: 20rem;
  width: 100%;
  border: 1px solid var(--gris);
}